.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

h4 {
  cursor: pointer;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}
.btn , .sortBtn{
  background-color: #000;
  border-radius: 5px;
  color: #fff;
  padding: 1px 5px;
  border: 1px solid aqua;
  margin-left: 10px;
  cursor: pointer;
}

.model {
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;  
}

.black-nav {
  display: flex;
  background-color: #000;
  width: 100%;
  color: #fff;
  padding-left: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
